// Modal.test.tsx - Jest 테스트용
import React from 'react';
import { render, screen, fireEvent } from '@testing-library/react-dom';
import '@testing-library/jest-dom';
import Modal from '../../src/components/Modal';

// 테스트용 컴포넌트
const TestContent = () => <div data-testid="modal-content">Test Content</div>;

// 테스트 렌더러 간단 구현
const customRender = (ui: React.ReactElement) => {
  const container = document.createElement('div');
  document.body.appendChild(container);
  render(ui, { container });
  return {
    getByTestId: (id: string) => container.querySelector(`[data-testid="${id}"]`),
    getByText: (text: string) => {
      const elements = Array.from(container.querySelectorAll('*'));
      return elements.find(el => el.textContent === text);
    },
    queryByText: (text: string) => {
      const elements = Array.from(container.querySelectorAll('*'));
      return elements.find(el => el.textContent === text) || null;
    },
    container
  };
};

describe('Modal Component', () => {
  const mockOnClose = jest.fn();
  
  beforeEach(() => {
    mockOnClose.mockClear();
    document.body.innerHTML = '';
  });
  
  it('renders nothing when not visible', () => {
    const { queryByText } = customRender(
      <Modal isVisible={false} onClose={mockOnClose}>
        <TestContent />
      </Modal>
    );
    
    expect(queryByText('Test Content')).toBeNull();
  });

  it('renders content when visible', () => {
    const { getByText } = customRender(
      <Modal isVisible={true} onClose={mockOnClose}>
        <TestContent />
      </Modal>
    );
    
    expect(getByText('Test Content')).not.toBeNull();
  });

  it('displays title when provided', () => {
    const { getByText } = customRender(
      <Modal isVisible={true} onClose={mockOnClose} title="Test Modal">
        <TestContent />
      </Modal>
    );
    
    expect(getByText('Test Modal')).not.toBeNull();
  });
  
  it('calls onClose when backdrop is pressed', () => {
    const { getByTestId } = customRender(
      <Modal isVisible={true} onClose={mockOnClose} closeOnBackdropPress={true}>
        <TestContent />
      </Modal>
    );
    
    const modalBackdrop = getByTestId('modal-backdrop');
    fireEvent.click(modalBackdrop as Element);
    
    expect(mockOnClose).toHaveBeenCalledTimes(1);
  });
  
  it('does not call onClose when backdrop is pressed and closeOnBackdropPress is false', () => {
    const { getByTestId } = customRender(
      <Modal isVisible={true} onClose={mockOnClose} closeOnBackdropPress={false}>
        <TestContent />
      </Modal>
    );
    
    const modalBackdrop = getByTestId('modal-backdrop');
    fireEvent.click(modalBackdrop as Element);
    
    expect(mockOnClose).not.toHaveBeenCalled();
  });
  
  it('calls onClose when close button is pressed', () => {
    const { getByTestId } = customRender(
      <Modal isVisible={true} onClose={mockOnClose} title="Test Modal">
        <TestContent />
      </Modal>
    );
    
    const closeButton = getByTestId('modal-close-button');
    fireEvent.click(closeButton as Element);
    
    expect(mockOnClose).toHaveBeenCalledTimes(1);
  });
  
  it('renders footer when provided', () => {
    const Footer = () => <div>Footer Content</div>;
    
    const { getByText, getByTestId } = customRender(
      <Modal 
        isVisible={true} 
        onClose={mockOnClose}
        footer={<Footer />}
      >
        <TestContent />
      </Modal>
    );
    
    expect(getByText('Footer Content')).not.toBeNull();
    expect(getByTestId('modal-footer')).not.toBeNull();
  });
});